import React, { useState, useEffect } from 'react'
import { Button, Header, Image, Modal, Icon } from 'semantic-ui-react'
import { withRouter } from 'react-router-dom';

class ModalModalExample extends React.Component {
  
  state = {
    open : false
  }

  componentDidMount(){
    // this.props.history.listen = (location,action) => console.log(location)
    // window.onhashchange = () => {
    //   console.log(location.hash == '#openMap' ? true : false)
    //   this.setState({open:location.hash == '#openMap' ? true : false})
    // }
  }

  openModal = () => this.setState({open:true})
  closeModal = () => this.setState({open:false})
  
  render(){
    return (
      <>
          <this.props.label onClick={this.openModal} />
          <Modal 
            open={this.state.open}
            onClose={this.closeModal}
          >
          <Modal.Content image>
              {this.props.children}
          </Modal.Content>
        </Modal>
      </>
    )
  }
} 

export default ModalModalExample